import { Directive, ElementRef, OnInit, OnDestroy } from '@angular/core';
import { fromEvent, Subscription } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { animationFrame } from 'rxjs/internal/scheduler/animationFrame';
@Directive({
  selector: '[fe-scroll-area]'
})
export class FEScrollAreaDirective implements OnInit, OnDestroy {
  // è®°å½æ»å¨äºä»¶
  private scrollSub: Subscription;
  // æ è®°å³è
  private relatedCollapseInstance;
  // è®°å½æ»å¨çå¼
  private scrollAreaTop = 0;
  // è®°å½å¼
  private threshold = 0;
  constructor(private el: ElementRef) {

  }
  ngOnInit() {
    this.bindScrollEvent();
  }
  ngOnDestroy() {
    this.scrollSub.unsubscribe();
    this.scrollSub = null;
  }

  // è¿½å å³è
  addCollapseInstance(cInstance) {
    this.relatedCollapseInstance = cInstance;
  }
  // ç»å®äºä»¶
  bindScrollEvent() {
    this.scrollSub = fromEvent(this.el.nativeElement, 'scroll').pipe(debounceTime(20)).subscribe((): void => {
      //å¤æ­æ»å¨çæ¹å
      this.scrollDirection();
    });
  }

  scrollDirection() {
    let afterScrollTop = this.el.nativeElement.scrollTop;
    let delta = afterScrollTop - this.scrollAreaTop;
    // æ²¡æåå
    if (delta === 0) return false;
    this.scrollAreaTop = afterScrollTop;
    this.judgeThreshold(delta > 0 ? "down" : "up");
  }

  private judgeThreshold(direction) {
    this.relatedCollapseInstance.updateHeight(this.scrollAreaTop, direction);

  }
}
